/* This CSS stylesheet is ordered by element > classes > IDs. */

/* ------------------- */
/* Formatting elements */
/* ------------------- */

body {
  font-family: "Inter", Arial, Helvetica, sans-serif;
  color: #2F323B;
  margin: 0;
}

.color-bg {
  background-image: url("assets/animated-cover.gif");
  background-size: 100%;
  background-repeat: no-repeat;
  background-color: #FEFAF6;
}

.white-bg {
  background-color: white;
}

p {
  font-size: 16px;
  line-height: 1.4;
}

h1 {
  font-size: 32px;
  font-weight: 600;
}

h2 {
  font-size: 18px;
  font-weight: 600;
}

a {
  font-weight: 700;
  text-decoration: none;
  color: #5253A3;
}

a:hover {
  color: #777CBB;
}

strong {
  font-weight: 600;
}

em {
  color: #97989D;  
}

img {
  width: 100%;
  height: auto;
}

footer {
  margin: 10vh 0;
}


/* ------------------- */
/* Formatting Classes */
/* ------------------- */

/* Images */
.lg-img {
  width: 60vw;
  height: auto;
}

.med-img {
  width: 100%;
  height: auto;
}

.small-img {
  width: 50%;
  height: auto;
}

.xs-img {
  width: 60%;
  height: auto;
}

.zotapotty.banner {
  width: 50vw;
  height: auto;
  padding-right: 150px;
}


/* Text */


.title h1 {
  font-size: 48px;
  font-weight: 400;
}

h1.intro {
  font-size: 48px;
  font-weight: 400;
  margin-bottom: 0;
}

h2.intro {
  font-size: 22px;
  font-weight: 400;
  margin-top: 1.5vh;
}

h1.card {
  font-size: 30px;
  font-weight: 600;
  color: black;
}

h1.card.no-hover:hover {
  color: black;
}

h1.card:hover {
  color: #777CBB;
}


p.card {
  margin-top: 0;
  margin-bottom: 20px;
}

p.button {
  text-transform: capitalize;
  line-height: 0%;
}

p.large {
  font-size: 20px;
}

.project h1 {
  padding-top: 100px;
}

.project strong {
  font-weight: 800;
}

.large-text {
  font-size: large;
}


/* List Items */

.horizontal-list.comp-analysis {
  padding: 12px 0;
}

.horizontal-list ul {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin: 0;
  padding: 0;
}

.horizontal-list.team ul {
  justify-content: stretch;
}

.horizontal-list li {
  list-style-type: none;
  padding-right: 24px;
}

.horizontal-list.team li {
  width: 220px;
}

.vertical-list ul {
  align-items: stretch;
  justify-content: space-between;
  width: 100%;
  margin: 0;
  padding: 0;
}

.vertical-list li {
  display: block;
  list-style-type: none;
  flex: 0 1 auto;
  padding-bottom: 10px;
}

.vertical-list p {
  line-height: 0.5;
}

.border {
  border: 2px solid #C7C7C7;
}

.problem-text {
  color: #D83232;
  font-weight: 600;
  margin-bottom: 6px;
}

.no-top-margin {
  margin-top: 0;
}

.no-bot-margin {
  margin-bottom: 0;
}

.floating-img {
  transition: ease 0.6s;
}

.floating-img:hover {
  transform: translateY(-6%);
  animation: floater 0.3s;
}

@keyframes floater {
  0%{transform: translateY(0%);}
  100%{transform: translateY(-6%);transition: ease 0.3s;}
}

.shadow {
  border-radius: 20px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.elevated-card {
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}


/* ------------------- */
/* Formatting IDs */
/* ------------------- */

#logo {
  /* font-family: "Montserrat", Arial, Helvetica, sans-serif;
  font-weight: 400;
  letter-spacing: 2px;
  text-transform: uppercase;
  text-decoration: none;
  color: black; */
  font-size: 28px;
  padding: 20px 0px 16px 0px;
}

#nav-links {
  text-align: right;
  float: right;
  padding: 10px 0px;
}

#nav-links ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  line-height: 2;
}

#nav-links li {
  display: inline;
}

#nav-links li a, #nav-links-mobile a {
  /* font-size: 80%; */
  font-size: 16;
  font-weight: 500;
  /* letter-spacing: 2px; */
  text-transform: lowercase;
  text-decoration: none;
  color: #2F323B;
  padding: 12px;
}

#nav-links li a:hover, #nav-links-mobile a:hover {
  color: #777CBB;
}

#purple-text {
  color: #6D56CB;
}

#numbered-list h1 {
  padding: 0;
  margin: 0;
}

#other-project h1 {
  font-size: 180%;
}

/* #comp-logo {
  width: 170px;
  height: auto;
} */

#footer-end {
  justify-content: end;
  margin: 21px 0;
}

#social-icons {
  justify-content: end;
}

#social-icons a {
  color: #2F323B;
}

#social-icons a:hover {
  color: #777CBB;
}



/* Media queries */
/* size: smaller than lg */
@media screen and (max-width: 1199px) {
  .title {
    padding-top: 12vw;
  }

  
/* size: xs (smaller than md) */
@media screen and (max-width: 899px) {
  
  h1.card {
    margin-top: 8px;
  }

  footer {
    padding: 0 8vw;
  }

  .title h1 {
    font-size: 36px;
  }

  h1 {
    font-size: 24px;
  }

  h2 {
    font-size: 16px;
  }

  p {font-size: 14px;}
  
  #footer-end {
    justify-content: flex-start;
    margin: 21px 0;
  }

  #social-icons {
    justify-content: start;
  }
  
}

/* idk made before */
/* size: xs smaller than sm) */
@media screen and (max-width: 599px) {
  #aboutme img, #aboutme p {
      padding: 15px 0px;
  }

}

}

